<template>
  <div>
    <nav class="navbar navbar-expand-lg navbar-light bg-light">
      <router-link to="/" class="navbar-brand" tag="a">Stock Trader</router-link>
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav mr-auto">
          <li class="nav-item">
            <router-link to="/portfolio" class="nav-link" tag="a" activeClass="active">Portfolio</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/stocks" class="nav-link" tag="a" activeClass="active">Stocks</router-link>
          </li>
        </ul>
        <div class="my-2 my-lg-0">
          <ul class="navbar-nav">
            <li class="nav-item">
              <a href="#" @click="endDay" class="nav-link">End Day</a>
            </li>
            <li class="nav-item dropdown">
              <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                Save & Load
              </a>
              <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                <a class="dropdown-item" href="#" @click="saveData">Save Data</a>
                <a class="dropdown-item" href="#" @click="loadData">Load Data</a>
              </div>
            </li>
            <strong class="navbar-text nav-link">Funds: {{ funds | currency}}</strong>
          </ul>
        </div>
      </div>
    </nav>
  </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    import { mapActions } from 'vuex'
    export default {
      computed: {
        ...mapGetters([
          'funds'
        ])
      },
      methods: {
        ...mapActions({
          randomizeStocks: 'randomizeStocks',
          fetchData: 'loadData'
        }),
        endDay() {
            this.randomizeStocks();
        },
        saveData() {
            const data = {
                funds: this.$store.getters.funds,
                stockPortfolio: this.$store.getters.stockPortfolio,
                stocks: this.$store.getters.stocks
            };
            this.$http.put('data.json', data)
        },
        loadData() {
          this.fetchData()
        }
      }
    }
</script>

<style>

</style>



