<template>
  <div>
    <div class="row">
    <app-stock v-for="stock in stockPortfolio" :stock="stock" :key="stock.id"></app-stock>
    </div>
  </div>
</template>

<script>
    import Stock from "./Stock";
    import { mapGetters } from 'vuex'
    export default {
      computed: {
        ...mapGetters([
          'stockPortfolio'
        ])
      },
      components: {
        appStock: Stock
      }
      }

</script>



